@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1100px;
  padding: 0 30px;
  margin: 0 auto;
  overflow: auto;
}

.content {
  flex: 1 1 10vh;
  padding-bottom: 1vh;
}

.form-error {
  display: none;
  color: red;
  font-size: 0.9rem;
  margin-top: 2px;
}

button {
  background: #81bb85e0;
  border: none;
  width: 9.375rem;
  height: 3.125rem;
  padding: 1rem;
  color: white;
  margin-top: 0.625rem;
  border-radius: 0.2rem;
  font-size: 1rem;
}

.btn-secondary, .btn-primary {
  display: inline-block;
  border-radius: 0.3125rem;
  padding: 0.5rem 1.25rem;
  margin: 0.1875rem;
  cursor: pointer;
}
.btn-secondary:hover, .btn-primary:hover {
  transform: scale(0.98);
}

.btn-primary {
  background-color: #65c095;
  color: #fff;
  border: none;
}

.btn-secondary {
  background-color: #81bb85e0;
  color: #fff;
  background: transparent;
  color: #81bb85e0;
  border: 2px solid #81bb85e0;
}

.cta {
  padding-top: 3rem;
}

.product-button {
  background: #81bb85e0;
  border: 0px;
  width: 100%;
  padding: 10px;
  margin: 20px 0px;
  transition: all 1s;
  font-weight: bold;
}

.product-button:hover {
  cursor: pointer;
}

/***** Cart  **********/
.cart {
  /* border: 1px solid rgb(105, 150, 105); */
  max-width: 600px;
  min-width: 200px;
  padding: 20px;
  margin: auto;
  position: fixed;
  right: 50px;
  /* top: 50px; */
  bottom: 100px;
  display: none;
  background: #d1c3c3;
  flex-direction: column;
  /* slett meg */
  color: #22222e;
}

.cart-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-size: cover;
}

.checkout-button {
  background: #006100;
  color: #884343;
  margin-top: 20px;
  padding: 5px;
  text-align: center;
  font-weight: bold;
  text-decoration: none;
}

.cart-page {
  max-width: 60%;
  margin: 0 auto;
  background: #2e679670;
  border-radius: 20px 0px;
  padding: 20px;
}

.cart-item {
  display: flex;
  justify-content: space-between;
}

.fa-shopping-cart:hover {
  color: black;
}

label {
  display: block;
  margin-top: 1rem;
}

input {
  width: 100%;
  margin-top: 2px;
  padding: 10px;
  font-size: 16px;
}

form {
  padding: 2em;
  background: transparent;
  border-radius: 1rem;
}

nav {
  display: flex;
  min-height: 10vh;
  align-items: center;
  justify-content: space-around;
  background-color: #f4f4f4;
}

.logo {
  color: #5f5f79;
  font-size: 20px;
  letter-spacing: 5px;
  display: flex;
  gap: 2em;
}
.logo img {
  max-height: 50px;
  max-width: 50px;
}

.nav-links {
  display: flex;
  width: 30%;
  justify-content: space-around;
}
.nav-links li {
  list-style: none;
}
.nav-links a {
  color: #5f5f79;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  letter-spacing: 5px;
}
.nav-links a:hover {
  color: #46ac7c;
}
.nav-links a:after {
  background-color: #46ac7c;
  content: "";
  position: absolute;
  height: 3px;
  width: 0;
  left: 0;
  bottom: -10px;
  transition: 0.3s;
  flex: -1;
}
.nav-links a:hover:after {
  width: 100%;
}

.burger {
  display: none;
  cursor: pointer;
}
.burger div {
  width: 25px;
  height: 4px;
  background-color: #5f5f79;
  margin: 4px;
  transition: all 0.7s ease;
}

/*******  Product *******/
.products {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 0 auto;
  padding: 4rem;
  grid-auto-flow: row;
}

.product {
  margin: 20px;
  padding: 20px;
  background: #fafafa6c;
  border-radius: 0px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.product:hover {
  box-shadow: 0 10px 18px 0 rgba(0, 0, 0, 0.2);
}

.product-image {
  background-size: cover;
  background-position: center;
  height: 150px;
  border-radius: 0px 20px;
}

.product-price {
  color: #46ac7c;
}

.product__image {
  max-width: 40vh;
}

@media screen and (max-width: 1024px) {
  .products {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media screen and (max-width: 768px) {
  .products {
    grid-template-columns: 1fr 1fr;
    padding: 0;
  }
}
@media screen and (max-width: 360px) {
  .products {
    grid-template-columns: 1fr;
    padding: 0;
  }
}
body {
  font-family: "Poppins", sans-serif;
  line-height: 1.6;
  font-weight: 500;
}

select {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

footer {
  font-weight: 700;
}

.product-price {
  font-weight: 300;
}

.intro-text h1 {
  font-size: 2.75rem;
  font-weight: 500;
}

.intro-text p {
  font-size: 22px;
}

html,
body {
  height: 100%;
}

body {
  background-color: #f4f4f4;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

a {
  color: #5f5f79;
  text-decoration: none;
  position: relative;
}

.logo {
  max-width: 6.25rem;
}

select {
  background-color: transparent;
  border: 2px solid #81bb85e0;
  text-align: center;
  padding: 0 1em 0 0;
  width: 9.375rem;
  height: 3.125rem;
  color: #81bb85e0;
  cursor: inherit;
}

option {
  background-color: #f4f4f4;
}

option:hover {
  color: chartreuse;
  background-color: blueviolet;
}

/* 
  BOB: 
*/
footer {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-shrink: 0;
  padding-top: 2vh;
  min-height: fit-content;
  gap: 1em;
  background: #f4f4f4;
  background: linear-gradient(180deg, #f4f4f4 0%, #bbc4cf 88%);
}

.footer-links > a {
  margin: 20px;
}

.legal,
.contact,
.social {
  display: flex;
  flex-direction: column;
}

.fab,
.far {
  padding: 0.125rem;
  margin: 0.125rem;
}

/* 
 BOB: 
 */
.presentation {
  display: flex;
  align-items: center;
  max-width: 90%;
  min-height: 80vh;
  margin: auto;
}

.introduction {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.intro-text h1 {
  background: linear-gradient(to right, #22222e, #9595b8);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.intro-text p {
  display: flex;
  flex-wrap: wrap nowrap;
  margin-top: 5px;
  color: #585772;
  width: 90%;
}

.cover {
  flex: 1;
  display: flex;
  justify-content: center;
  height: 60vh;
}

.cover img {
  height: 100%;
  filter: drop-shadow(0px 5px 3px black);
  animation: drop 1.5s ease;
}

.cover__about {
  flex: 1;
  display: flex;
  justify-content: center;
  height: 60vh;
}

.cover__about img {
  height: 55vh;
  animation: drop 1.5s ease;
  border-radius: 0px 20px;
}

.cover__contact {
  flex: 1;
  display: flex;
  justify-content: center;
  height: 60vh;
}

/* Contact form END */
@media screen and (max-width: 1024px) {
  .nav-links {
    display: flex;
    width: 60%;
    justify-content: space-around;
  }

  .presentation {
    flex-direction: column;
  }

  .introduction {
    margin-top: 5vh;
    text-align: center;
  }

  .intro-text h1 {
    font-size: 1.875rem;
    text-align: center;
  }

  .intro-text p {
    font-size: 1.125rem;
    text-align: center;
  }

  .cover img {
    max-width: 40vw;
  }

  .cover__about img {
    max-height: 60vh;
  }
}
@media screen and (max-width: 768px) {
  .nav-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    right: 0px;
    height: 92vh;
    top: 8vh;
    width: 50%;
    background-color: #f4f4f4;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
  }

  .nav-links li {
    opacity: 0;
  }

  .burger {
    display: block;
  }

  .cover img {
    height: auto;
  }

  .cover__about img {
    max-height: 40vh;
  }
}
@media screen and (max-width: 360px) {
  .presentation {
    flex-direction: column;
  }

  .introduction {
    margin-top: 5vh;
    text-align: center;
  }

  .intro-text h1 {
    font-size: 1.875rem;
  }

  .intro-text p {
    font-size: 1.125rem;
  }

  .cover img {
    height: auto;
  }

  .cover__about img {
    max-height: 20vh;
  }
}
.nav-active {
  transform: translateX(0%);
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes drop {
  0% {
    opacity: 0;
    transform: translateY(-80px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
  opacity: 0;
}

.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}